.main-header {
  width: 100%;
  z-index: 1;
}

.main-header__wrapper {
  display: flex;
  flex-wrap: wrap;
  padding: 13px 20px;
  align-items: center;
  justify-content: space-between;
  z-index: 1;

  @media (min-width: @tablet-width) {
    padding: 25px 30px 0 30px;
  }

  @media (min-width: @desktop-width) {
    padding: 55px 110px 0 110px;
    max-width: 1440px;
  }
}

.main-header__title {
  display: contents;

  @media (min-width: @tablet-width) {
    display: flex;
    align-items: center;
  }
}

.main-header__title img {
  width: 33px;
  height: 38px;

  @media (min-width: @tablet-width) {
    width: 60px;
    height: 50px;
  }

  @media (min-width: @desktop-width) {
    width: 70px;
    height: 59px;
  }
}

.main-header__container-logo img {
  width: 101px;
  height: 18px;

  @media (min-width: @desktop-width) {
    width: 118px;
    height: 21px;
  }
}

@media (min-width: @tablet-width) {
  .main-header__container-logo {
    display: flex;
    margin-left: 13px;

    @media (min-width: @desktop-width) {
      margin-left: 14px;
    }
  }
}

.main-nav__item {
  display: block;
}

@media (min-width: @tablet-width) {
  .main-header__logo-text {
    margin: 14px 0;
    margin-left: 13px;

    &:hover {
      opacity: 0.7;
    }

    &:hover:active {
      opacity: 0.5;
    }
  }
}

@media (min-width: @desktop-width) {
  .main-header__logo-text-desktop {
    margin: 17px 0;
    margin-left: 14px;

    &:hover {
      opacity: 0.7;
    }

    &:hover:active {
      opacity: 0.5;
    }
  }
}

.main-nav--closed .main-nav__toggle-button {
  padding: 15px 0;
  padding-left: 10px;
  border: none;
  cursor: pointer;
  background: none;

  &:hover {
    opacity: 0.7;
  }

  &:hover:active {
    opacity: 0.5;
  }
}

.main-nav--closed .main-nav__toggle {
  display: block;
  width: 24px;
  height: 2px;
  background-color: @lightgrey;
  box-shadow: 0 -8px 0 0 @lightgrey, 0 8px 0 0 @lightgrey;

  @media (min-width: @tablet-width) {
    display: none;
  }
}

@media (max-width: @mobile-width-only) {
  .main-nav--nojs .main-nav__toggle-button {
    display: none;
  }

  .main-nav--nojs .main-nav__wrapper {
    position: static;
    min-height: 0;
  }

  .main-nav--nojs .main-nav__item {
    display: block;
  }

  .main-nav--nojs .main-nav__item--active .main-nav__title {
    text-align: center;
  }
}

.main-nav--opened .main-nav__toggle-button {
  padding: 15px 0;
  padding-left: 10px;
  border: none;
  cursor: pointer;
  background: none;

  &:hover {
    opacity: 0.7;
  }

  &:hover:active {
    opacity: 0.5;
  }
}

.main-nav--opened .main-nav__toggle {
  display: block;
  width: 25px;
  height: 2px;
  background-color: @lightgrey;
  box-shadow: 3px 0 0 0 @lightgrey;
  transform: rotate(45deg);
}

.main-nav--opened .main-nav__toggle-clon {
  display: block;
  width: 25px;
  height: 2px;
  background-color: @lightgrey;
  box-shadow: 3px 0 0 0 @lightgrey;
  transform: rotate(-45deg);
}

.main-nav--opened .main-nav__wrapper {
  position: absolute;
  top: 50px;
  right: 0;
  left: 0;
  z-index: 10;

  @media (min-width: @tablet-width) {
    display: contents;
  }
}

@media (min-width: @tablet-width) {
  .main-nav__item--active::after {
    content: "";
    position: relative;
    display: block;
    top: 10px;
    border-bottom: 2px solid #68b738;

    @media (min-width: @desktop-width) {
      background-color: none;
      border-bottom: 2px solid #ffffff;
    }
  }
}

.main-nav__list {
  width: 100%;
  text-align: center;
  background-color: @basic-white;

  @media (min-width: @tablet-width) {
    display: flex;
    flex-wrap: wrap;
    justify-content: end;
    gap: 33px;

    @media (min-width: @desktop-width) {
      display: flex;
      width: 476px;
      z-index: 1;
      background-color: transparent;
    }
  }
}

.site-list .main-nav__title {
  display: block;
  position: relative;
  margin: 0;
  padding: 22px 0;
  padding-bottom: 24px;
  top: 15px;
  font-weight: 400;
  font-family: "Oswald", sans-serif;
  font-size: 20px;
  line-height: 20px;
  color: @lightgrey;
  text-transform: uppercase;
  text-decoration: none;
  border-top: 1px solid @Center_alignment;

  @media (min-width: @tablet-width) {
    display: contents;
    font-size: 18px;
    line-height: 24px;
    border: none;
  }

  @media (min-width: @desktop-width) {
    font-size: 20px;
    line-height: 30px;
    background-color: transparent;
    color: #111111;
  }
}

@media (min-width: @desktop-width) {
  .site-list .main-nav__title-catalog {
    color: #ffffff;
  }

  .main-nav__item--active-catalog::after {
    content: "";
    position: relative;
    display: block;
    top: 10px;
    border-bottom: 2px solid #68b738;
  }
}

.main-nav {
  display: contents;

  @media (max-width: @tablet-width) {
    display: block;
  }
}

@media (max-width: @tablet-width) {
  .main-nav--closed .main-nav__item,
  .main-header__logo-text-desktop {
    display: none;
  }
}

@media (min-width: @tablet-width) {
  .main-header__logo,
  .main-nav--opened .main-nav__toggle,
  .main-nav--opened .main-nav__toggle-clon,
  .main-nav__toggle-button {
    display: none;
  }
}

@media (max-width: @desktop-width) {
  .main-header__logo-text-desktop {
    display: none;
  }
}

@media (min-width: @desktop-width) {
  .main-header__logo-text,
  .main-nav--opened .main-nav__toggle,
  .main-nav--opened .main-nav__toggle-clon,
  .main-nav__toggle-button {
    display: none;
  }
}
